<template>
	<div class="ratingselect">
		<div class="rating-type">
			<span @click="select(2,$event)" class="block positive" :class="{'active':aa===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
			<span @click="select(0,$event)" class="block positive" :class="{'active':aa===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
			<span @click="select(1,$event)" class="block negative" :class="{'active':aa===1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
		</div>
		<div @click="toggleContent($event)" class="switch" :class="{'on':bb}">
			<span class="icon-check_circle"></span>
			<span class="text">只看有内容的评价</span>
		</div>
	</div>
</template>

<script>
	const POSITIVE=0;
	const NEGATIVE=1;
	const ALL=2;
	
	export default{
		props:{
			ratings:{
				type:Array,
				default(){
					return []
				}
			},
			selectType:{
				type:Number,
				default:ALL
			},
			onlyContent:{
				type:Boolean,
				default:false
			},
			desc:{
				type:Object,
				default(){
					return{
						all:"全部",
						positive:"满意",
						negative:"不满意"
					}
				}
			}
		},
		computed:{
			positives(){
				return this.ratings.filter((rating)=>{
					return rating.rateType ===POSITIVE
				})
			},
			negatives(){
				return this.ratings.filter((rating)=>{
					return rating.rateType ===NEGATIVE
				})
			}
		},
		data(){
			return{
				aa:this.selectType,
				bb:this.onlyContent
			}
		},	
		methods:{
			select(type,event){
				if(!event._constructed){
					return ;
				}
				this.aa=type
				this.$emit("ratsel",type)
			},
			toggleContent(event){
				if(!event._constructed){
					return ;
				}
				this.bb=!this.bb
				this.$emit("togcont",this.bb)
			}
		}
	}
</script>

<style scoped="">
	.ratingselect .rating-type{
		padding: 18px 0;
		margin: 0 18px;
		border-bottom:1px solid rgba(7,17,27,0.1);
		font-size: 0;
	}
	.block{
		display: inline-block;
		padding:8px 12px;
		border-radius: 2px;
		margin-right: 8px;
		color: rgb(77,85,93);
		font-size: 12px;
	}
	.rating-type .block.active{
		color: #fff;
	}
	.count{
		font-size: 8px;
		margin-left: 2px;
		line-height: 16px;
	}
	.positive{
		background: rgba(0,160,220,0.2);
	}
	.rating-type .positive.active{
		background: rgb(0,160,220);
	}
	.negative{
		background: rgba(77,85,93,0.2);
	}
	.rating-type .negative.active{
		background: rgb(77,85,93);
	}
	.switch{
		padding:12px 18px;
		line-height: 24px;
		color: rgb(147,153,159);
		border-bottom:1px solid rgba(7,17,27,0.1);
		font-size: 0;
	}
	.switch.on .icon-check_circle{
		color: #00c850;
	}
	.switch .icon-check_circle{
		display: inline-block;
		vertical-align: top;
		font-size: 24px;
		margin-right: 4px;
	}
	.switch .text{
		display: inline-block;
		vertical-align: top;
		font-size: 12px;
	}
</style>


